<template>
  <div>
    <el-row :gutter="24">
      <el-col :span="12">
        <div class="typed-container">
          <span id="typed-element"></span>
        </div>
      </el-col>
      <el-col :span="12">
        <div class="heart"></div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import Typed from "typed.js";
import moment from 'moment'
export default {
  data() {
    return {
      date: '2022-07-01 00:00:00'
    }
  },
  computed: {
    textArr() {
      const dateStr = moment(this.date).format('YYYYMMDD')
      const days = moment().diff(moment(this.date), 'days')
      let result = `${dateStr}${days}<br/><br/>20220723<br/><br/>20220910<br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/>`
      return [result]
    }
  },
  mounted() {
    const options = {
      strings: this.textArr,
      typeSpeed: 50,
      startDelay: 1000,
      backSpeed: 0,
      smartBackspace: true,
      shuffle: false,
      backDelay: Infinity,
      fadeOut: true,
      fadeOutClass: "typed-fade-out",
      fadeOutDelay: 500,
      loop: false,
      loopCount: Infinity,
      showCursor: true,
      cursorChar: "_",
      autoInsertCss: true,
      attr: null,
      bindInputFocusEvents: false,
      contentType: "html",
      onBegin: self => {},
      onComplete: self => {},
      preStringTyped: (arrayPos, self) => {},
      onStringTyped: (arrayPos, self) => {},
      onLastStringBackspaced: self => {},
      onTypingPaused: (arrayPos, self) => {},
      onTypingResumed: (arrayPos, self) => {},
      onReset: self => {},
      onStop: (arrayPos, self) => {},
      onStart: (arrayPos, self) => {},
      onDestroy: self => {},
    };
    new Typed("#typed-element", options);
  },
  methods: {
  }
};
</script>

<style lang="scss" scoped>

.heart {
  --c: red;
  width: 600px;
  aspect-ratio: 1;
  background:
    /* 1 */ radial-gradient(circle at 60% 65%, var(--c) 64%, #0000 65% ) top left/50% 50%,
    /* 2 */ radial-gradient(circle at 40% 65%, var(--c) 64%, #0000 65%) top right/50% 50%,
    /* 3 */ conic-gradient(from -45deg at 50% 85.5%, var(--c) 90deg, #0000 0) bottom/100% 50%;
  background-repeat: no-repeat;
  margin: 200px auto;
  animation: beat 0.7s ease infinite;
}
@keyframes beat {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.1);
  }

  100% {
    transform: scale(1);
  }
}
.typed-container {
  padding: 60px 20px 100px 100px;
}
#typed-element {
  line-height: 40px;
  color: red;
  font-size: 18px;
}
</style>
<style lang="scss">
.typed-cursor {
  color: red;
}
</style>
